@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.9/dist/web/static/pretendard.min.css");

@font-face {
  font-family: 'KyoboHandwriting2023wsa';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/2404-2@1.0/KyoboHandwriting2023wsa.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Pretendard Light", sans-serif;
  background: rgb(24, 24, 24);
  color: #eeeeee;
}

button {
  background: none;
  border-style: none;
  color: #eeeeee;
}

.App {
  width: 100vw;
  overflow-y: scroll;
}

#navigation-bar {
  height: 50px;
  width: 100vw;
  position: fixed;
  top: 0;
  padding: 10px 30px;
}

#liferary-logo {
  font-size: 24px;
  font-weight: 600;
  line-height: 50px;
  height: 50px;
  float: left;
}

nav#navigation-bar ul {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin: 0;
  float: left;
}

nav#navigation-bar ul li {
  padding-right: 30px;
  height: 50px;
  line-height: 50px;
  font-size: 16px;
}

nav#navigation-bar span#mypage {
  background: rgb(44, 44, 44);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 1px solid white;
  display: block;
  position: relative;
  float: right;
  margin-right: 60px;
}

span#mypage>img#my-profile {
  width: 50px;
  height: 50px;
}

#tag-content {
  width: 100vw;
  height: 100vh;
  position: absolute;
  display: flex;
  justify-content: flex-start;
  flex-wrap: nowrap;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  padding-top: 30px;
}

#tag-title {
  margin: 0;
  font-size: 36px;
  font-family: 'Pretendard ExtraBold', sans-serif;
}

#tag-list-content {
  width: 900px;
  height: 40px;
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

#tag-list-content>button {
  width: 20px;
  height: 32px;
  font-family: 'Pretendard SemiBold', sans-serif;
  line-height: 32px;
  text-align: center;
  font-size: 20px;
}

#tag-list {
  width: 850px;
  height: 32px;

  overflow: hidden;
  position: relative;


}

#tag-list-wrapper {
  display: flex;
  position: absolute;
  width: 100%;
}

#tag-list-wrapper>section {
  display: flex;
  animation: swipe 30s linear infinite;
}

@keyframes swipe {
  0% {
    transform: translate(0);
  }

  100% {
    transform: translate(-100%);
  }
}

#tag-list-wrapper>section>.tag {
  width: 70px;
  height: 32px;
  border-radius: 16px;
  line-height: 32px;
  text-align: center;
  font-family: 'Pretendard SemiBold', sans-serif;
  margin: 0 25px;
  float: left;
  font-size: 16px;
}

#tag-list-gradient-left {
  width: 50px;
  height: 32px;
  position: absolute;
  left: 0;
  background-image: linear-gradient(90deg, rgba(24, 24, 24, 1) 0%, rgba(24, 24, 24, 0) 100%);
}

#tag-list-gradient-right {
  width: 50px;
  height: 32px;
  position: absolute;
  right: 0;
  background-image: linear-gradient(-90deg, rgba(24, 24, 24, 1) 0%, rgba(24, 24, 24, 0) 100%);
}

