#box-section {
  width: 900px;
  height: 120px;
  margin-top: 50px;
  display: flex;
  justify-content: flex-start;
}

#box-image {
  width: 120px;
  height: 120px;
  margin-right: 20px;
}

#box-image>img {
  width: 120px;
}

#box-image>img:hover {
  animation: shaking-box 1.2s ease-in-out infinite both;
}

@keyframes shaking-box {
  0% {
    transform: translate(0, 0) rotate(0deg);
  }

  5% {
    transform: translate(-2px, -5px) rotate(-10deg);
  }

  15% {
    transform: translate(2px, -5px) rotate(10deg);
  }

  20% {
    transform: translate(0, -4px) rotate(0deg);
  }

  25% {
    transform: translate(-2px, -5px) rotate(-10deg);
  }

  35% {
    transform: translate(2px, -5px) rotate(10deg);
  }

  50% {
    transform: translate(0, 4px) rotate(-5deg);
  }

  60% {
    transform: translate(0, 0) rotate(0deg);
  }
}

#box-recommend-content {
  width: 760px;
  height: 120px;
}

#box-mention {
  height: 30px;
  line-height: 30px;
  margin-bottom: 5px;
}

#box-mention>h3 {
  font-family: 'Pretendard SemiBold', sans-serif;
  font-size: 20px;
}

#box-recommend-content p {
  height: 30px;
  margin: 0;
  display: block;
  line-height: 30px;
  color: #dddddd;
}

#number-section {
  width: 900px;
  height: 50px;
  margin-top: 50px;
  display: flex;
  justify-content: center;
}

#number-section>button {
  margin: 0 5px;
  color: #cccccc;
}



