#user-writing-section {
  width: 900px;
  height: 900px;
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.user-writing {
  width: 430px;
  height: 430px;
  margin: 10px 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  background-color: rgba(255, 255, 255, 0.05);
  position: relative;
}

.writing-bg-gradient {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 50%;
  background-image: linear-gradient(180deg, rgba(24, 24, 24, 0) 0%, rgba(24, 24, 24, 1) 100%);
}

.user-writing>h4 {
  font-family: 'Pretendard SemiBold', sans-serif;
  font-size: 18px;
  margin: 0 0 0 15px;
  z-index: 1;
}

.user-writing>p {
  font-size: 16px;
  color: #cccccc;
  display: block;
  margin: 10px 0 30px 15px;
  z-index: 1;
}


